import React from 'react';
import Navbar from '../components/Navbar';
import '../pages/Guide.css';
import { ReactComponent as Format } from '../Images/format.svg';

function GuideForm() {
 return (
  <div className="heroform">
    <Navbar label="Guide" />
    <a href="/guide/instructions" className="suiv">Suivant</a>
    <a href="/guide/architecture" className="prev">Retour</a>
    <h2 className='h2'>Format d'instruction</h2>
    <Format className='Formatinst'/>
    <p className='text'>
    - COP : Le code opration sur 6 bits, donc 64 instructions.<br/>
    - MOD : Le mode d'adressage du 2me oprande.<br/>
    - R : il contient le code du registre intermdiaire dans le cas des instructions mmoire-mmoire, ou CO dans le cas des branchements.<br/>
    - C : il est utilit comme un 2me registre, sinon dans les instructions de branchement (condition), et de dcalages (bits  dcaler)..
    </p>
  </div>
 );
}


export default GuideForm;